const animation = {
    getScrollTop() {
        return document.body.scrollTop || document.documentElement.scrollTop
    },
    getClientWidth() {
        return window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth
    },
    // Body
    getPoint(ele) {
        var t = ele.offsetTop;
        let parent = ele.offsetParent
        if (parent.nodeName !== 'BODY') {
            t += this.getPoint(parent)
        }
        return t
    },
    show(ele, distance, action) {
        let scrollTop = this.getScrollTop()
        if (scrollTop >= distance) {
            ele.classList.add('animate__animated', action);
            ele.style.visibility = 'visible'
        }
    },
    text_about: 400 // 500text_about
};

const getFullYear = (date) => {
    let year = date.getFullYear()
    let month = date.getMonth()
    let day = date.getDate()
    return {
        year,
        month,
        day
    }
}

const getDate = (year, month, day) => {
    return new Date(year, month, day)
}

  /**   */
const goTo = function(target){
    if ($('.shade').hasClass('global-shade')) return;
    $('.shade').addClass('global-shade')
    target.css({ 'transform': 'translateX(25%)' })
}

 /**     */
const goBack = function(target) {
    target.css({ 'transform': 'translateX(300%)' })
    $('.shade').removeClass('global-shade')
}

export {
    animation,
    getFullYear,
    getDate,
    goTo,
    goBack
}

